import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { AfterloginService } from 'src/app/_services/afterlogin.service';
import { ActivatedRoute, Router } from '@angular/router';
import { interval, Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';

@Component({
    selector: 'app-header',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
  })
  export class LoginComponent implements OnInit {
    isUserlogedin=false;
    SignUpForm:FormGroup 
  firstObsubscription: any;
    constructor(private http:HttpClient, public checkservice:AfterloginService,private route: ActivatedRoute,private router: Router,) { }
  
    ngOnInit() {
      this.isUserlogedin=false;
      this.checkservice.isUserlogedincheck.next(false)
      this.SignUpForm= new FormGroup({UserName: new FormControl(null),Password: new FormControl(null),})
    }
    onSubmit(){
      debugger
      let o=this.SignUpForm.value
       this.http.post('http://gpstrack.mahamining.com/api/UserDetails/CheckLogin_1_0',o)
       .subscribe(dt => {
          localStorage.info=(JSON.stringify(dt))
          this.checkservice.isUserlogedincheck.next(true)
          this.router.navigate(['/dashboard']);
          },error => {
            alert(error.error.Message+'\n'+ error.message)
            }
       )

   
    }
  
  }